//抽离 轻提示Toast 无需再使用self.$toast
import {Toast} from 'vant'
 
//1.创建类 设置验证表单数据
class ValidForm {
    constructor(){
        //在register.vue控制位数maxlength
        //2.nickNme设置1-10，汉字字母数字_组合
        //6.整合Reg方便提示传递msg
        this.validUserReg ={
            nickName:{
                reg:/^[\u4e00-\u9fa5\w]{1,10}$/,
                msg:'昵称支持汉字字母数字组合，1-10个字符'
            } ,
            phone:{
                reg:/^1[3-9]\d{9}$/,
                msg:'手机号格式不正确'
            } ,
            password:{
                reg:/^[a-zA-Z]\w{5,15}$/,
                msg:'密码首字母为英文'
            } ,

        }
        //this.nickNameReg = /^[\u4e00-\u9fa5\w]{1,10}$/;
        // this.phoneReg = /^1[3-9]\d{9}$/;
        // this.passwordReg = /^[a-zA-Z]\w{5,15}$/;
    }
    //3验证表单 对象
    validUser(obj){
        for(let key in obj){
            // 1.如果有一个验证失败的话this.nickNameReg 2.存放在整合对象validUserReg
            // if(!this[key +'Reg'].test(obj[key])){
            if(!this.validUserReg[key].reg.test(obj[key])){
                
                
                //2.这里的this指向validUserReg 所以需要再传递参数self 然后调用关联register.vue的方法 register传递this  。 Vue 的 prototype 上挂载 $toast()方法，便于在组件内调用. 也可使用导入import
                    Toast(this.validUserReg[key].msg);

                return false;
            }
        }
        //验证通过
        return true;
    }

}

//4导出类 表单数据
export default new ValidForm();
//5.import导入数据 至 register.vue