<template>
  <div class="register">
    <van-nav-bar title="登录" left-text="注册" right-text="关闭" @click-left="goPage('Register')" />
    <div class="logo">
      <img class="auto-img" src="../assets/images/logo.png" alt />
    </div>

    <van-form @submit="login">
     
      <van-field
        v-model="userInfo.phone"
        type="text"
        label="手机号"
        placeholder="请输入11位的手机号"
        left-icon="phone-o"
        maxlength="11"
      >
       
        <template v-slot:right-icon>
          <i class="fa fa-camera-retro fa-lg"></i>
        </template>
      </van-field>

      <van-field
        v-model="userInfo.password"
        :type="isSee ? 'text':'password'"
        label="密码"
        placeholder="首字母为英文16位数字字母组合"
        left-icon="lock"
        :right-icon="isSee ? 'eye-o':'closed-eye'"
        @click-right-icon="seePassword"
        maxlength="16"
      />
      <div class="register-btn">
        <van-button round block type="info" native-type="submit" color="rgb(249,187,50)">立即登录</van-button>
      <div class="forgot">
          <span class="fr">忘记密码？</span>
      </div>

      </div>
    </van-form>
  </div>
</template>

<script>

import validForm from "../assets/js/formTest";

import '../assets/css/register.less'


export default {
  data() {
    return {
      userInfo: {

        phone: "",
        password: ""
      },
      isSee: false
    };
  },
  methods: {
    //跳转注册
    goPage(name){
      this.$router.push({name});
    },
    seePassword() {
      this.isSee = !this.isSee;
    },
    //4.登录
    login() {
      if (validForm.validUser(this.userInfo)) {
        
        let data = Object.assign({}, this.userInfo);
        data.appkey = this.appkey;
        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          duration:0 
        });

        this.axios({
          method: "POST",
          url: "/login",
          data 
        })
          .then(result => {
            
            this.$toast.clear();
            //跳转到菜单界面
            if(result.data.code == 200){
              
              localStorage.setItem('token',result.data.token);
              this.$router.push({ name: "Menu" });
            }else{
              this.$toast(result.data.msg);//否则传递信息msg
            }
          })
          .catch(err => {
            
            this.$toast.clear();
          });
      }
    }
  }
};
</script>
<style lang="less" scoped>
.forgot{
    
    margin-top: 20px;
}
</style>
<style lang="less" scoped>

.van-nav-bar {
  background-color:rgb(249,187,50);
  color: #f0f0f0;
}
/deep/ .van-nav-bar__text {
    color: #f0f0f0;
}
</style>